body {
  direction: rtl;
}

.page-container {
  background-color: #f8f6f68e;
  border-radius: 12px;
  align-items: center;
}

.card-container {
  background-color: #f8f6f68e;
  border-radius: 12px;
  width: 50rem;
  margin-top: 5rem;
  padding: 2rem 0.5rem;
  align-items: center;
}

@media only screen and (max-width: 600px) {
  .page-container {
    background-color: #f8f6f68e;
    border-radius: 12px;
    align-items: center;
  }

  .card-container {
    background-color: #f8f6f68e;
    border-radius: 12px;
    width: 50rem;
    margin-top: 5rem;
    padding: 2rem 0.5rem;
    align-items: center;
  }

  img {
    max-width: 30%;
  }
}
